.header {
  padding: 25px;
}

.header__menu-item {
  font-family: "GothamProRegular", sans-serif;
  font-size: 14px;
  color: #ED6C0C;
  text-transform: uppercase;
  display: block;
  line-height: 52px;
  padding: 0 9px;
}

.header__menu-item:last-child {
  padding-right: 0;
}

.header__menu-item:first-child {
  padding-left: 0;
}

.header__menu-item_white {
  color: white;
}

.header__small-item {
  display: block;
  line-height: 52px;
  font-family: "GothamProMedium", sans-serif;
  font-size: 14px;
  color: #fff;
  padding: 0 18px;
}

.header__small-item:last-child {
  padding-right: 0;
}

.header__small-item:first-child {
  padding-left: 0;
}

.header__arrow-after::after {
  content: url("../img/header/small-arrow.svg");
  display: inline-block;
  margin-left: 6px;
}

.small-item__img {
  display: block;
  margin-top: 14px;
  position: relative;
  z-index: 5;
}

.small-item__cage {
  position: relative;
}

.small-item__cage::after {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #E68336;
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 1;
}

